//--------- display flex ----------
.dflex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.dflex_col {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.dflex_start {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.dflex_col_start {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.dflex_top {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

//--------- display flex ----------
*::-webkit-scrollbar {
    width: 0px;
}

h1 {

    font-size: 2rem;
    letter-spacing: 0.9px;
    background: linear-gradient(25deg, rgb(68 77 255) 0%, rgb(194 92 255) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    width: fit-content;
}

//--------- colors ----------
.text-lite {
    color: rgba(170, 170, 170, 0.765);
}

//--------- colors ----------

input {
    padding: .45em 1.2em;
    border-radius: 5px 0px 0px 5px;
    border: 1px solid rgb(71, 71, 71);
}

a {
    font-weight: 500;
    color: #878dff;
    text-decoration: inherit;
}

a:hover {
    color: #535bf2;
}

//---------- button --------
button {
    border-radius: 5px;
    border: 1px solid transparent;
    padding: 0.45em 1.2em;
    font-size: 1em;
    font-weight: 500;
    font-family: inherit;
    background-color: #1a1a1a;
    cursor: pointer;
    transition: border-color 0.25s;
}

button:hover,
input:hover,
input:focus {
    border-color: #646cff;
}

button:focus,
button:focus-visible,
input:focus,
input:focus-visible {
    border: 1px solid white;
    outline: 0px;
}

.searchbtn {
    border-radius: 0px 5px 5px 0px;
}

//---------- button --------
