//--------- Nevbar -----------
.Nevbar {
    // position: fixed;
    top: 0px;
    left: 0px;
    background-color: #1a1a1a;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 5;

    .NevLinks {
        margin: auto 0px auto auto;
        // align-items: end;
        padding: 10px 5px;
        list-style: none;

        li {
            // list-style: none;
            display: inline-flex;
            padding: 0px 5px;
        }
    }
}

a.active {
    @apply bg-slate-950
}

.logoimg {
    // width: 100px;
    filter: drop-shadow(0px 0px 4px #9fff9e);
}

.nevlink.active {
    color: #d115d1;
}

